    <vue-star animate="animated tada" color="#F05654">
      <a slot="icon" class="fa fa-heart"></a>
    </vue-star>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.VueStar {
  position: unset
}
</style>



